import React, {Component} from 'react';

import ViewInicio from './views/viewInicio';
import ViewLogin from './views/viewLogin';
import ViewAnalisisEmpresa from './views/viewAnalisisEmpresa';
import ViewAnalisisMarcas from './views/viewAnalisisMarcas';

class ClassNavegar extends Component {

    constructor(props){
        super(props);
        this.setVista = this.setVista.bind(this);
        this.state = {
            vista:this.props.vista,
            empnit:this.props.empnit            
        }
     
    }


    setVista(vista){
        console.log(vista)
        this.setState({vista:vista})
    }

    setEmpnit = (empnit)=>{
        this.setState({empnit:empnit})
    }


    render() {
        switch (this.state.vista) {
            case 'LOGIN':
                return (<ViewLogin usuario="" />)    
                break;
            case 'INICIO': 
                return(<ViewInicio usuario={this.props.user} vista={this.setVista} empnit={this.setEmpnit} />)
                break;
            case 'ANALISIS_EMPRESA':
                return(<ViewAnalisisEmpresa empnit={this.state.empnit} vista={this.setVista} />)
                break;
            case 'ANALISIS_PRODUCTOS':
                return(<h2>Navegando a PRODUCTOS</h2>);
                break;
            case 'ANALISIS_MARCAS':
                return(<ViewAnalisisMarcas vista={this.setVista} />);
                break;    
            case 'ANALISIS_MAPA':
                return(<h2>'Navegando a MAPA'</h2>);
                break;    
            case 'ANALISIS_HISTORICO':
                return(<h2>'Navegando a HISTORICO'</h2>);
                break;

            default:
                break;
        }       
    }
}


export default ClassNavegar;