import React, {Component} from 'react';
import axios from "axios";
import funciones from '../funciones';
import ViewLoading from '../partials/partialLoading';
import { PieChart, Pie,Cell, LabelList,Legend, Tooltip,ResponsiveContainer } from "recharts";



let api = 'https://mercadosefectivos-bi-api.herokuapp.com';

class ViewInicio extends Component {
    constructor(props){
        super(props);
        //this.menu = React.createRef()
        this.state = {
            empresas: [],
            menu:'circular-menu',
            loading:false
        };
    }
    //this.props.usuario
    onLogout = () => {
        this.props.vista("LOGIN");
    }

    onGetMenu = (opcion)=>{
        this.props.vista(opcion);
    }

    onGetEmpresa= (empnit) => {
        this.props.empnit(empnit);
        this.props.vista('ANALISIS_EMPRESA');
    }
   
    toggleMenu=()=> {
        var css = ''
        if(this.state.menu==="circular-menu"){
            css="circular-menu active"
        }else{
            css="circular-menu"
        };
        this.setState({"menu":css});
    }

    componentDidMount() {
        this.setState({"loading":true});

        axios.get(`${api}/getempresas`)
        .then(res => {
          const empresas = res.data.recordset;
          //console.log(empresas);
          this.setState({"empresas": empresas,"loading":false});
          //return empresas;
        })
        .catch(()=>{
            //return [];
            this.setState({"loading":false});
        })
    }

    render(){
        let data = this.state.empresas;
        console.log(data);
        if(this.state.loading==true){
            return(<ViewLoading />);
        }else{
            return (<div>      
                    <div className="row card-deck">
                        {this.state.empresas.map((r)=>{
                            let strclass = "card border-top-rounded border-bottom-rounded shadow hand p-10 mb-3 px-3 text-secondary border-secondary "
                            return (<div className="col-6" key={r.EMPNIT}>
                                        <div className={strclass} onClick={ ()=>{this.onGetEmpresa(r.EMPNIT)}}>
                                            <div className="card-body">
                                                <div className="row">
                                                    <div className="col-3">
                                                        <img src={'./favicon.png'} width="50" height="50"></img>
                                                    </div>
                                                    <div className="col-9">
                                                        <br></br>
                                                        <h3 className="text-secondary">{r.NOMBRE}</h3>
                                                    </div>
                                                </div>
                                                
                                                <div className="row border-bottom border-gris">
                                                    <div className="col-4">
                                                        <label>Ventas:</label>
                                                    </div>
                                                    <div className="col-8">
                                                        <label><b>{funciones.setMoneda(r.VENTAS,'Q')}</b></label>    
                                                    </div>
                                                </div>
                                            
                                                <div className="row border-bottom border-gris">
                                                    <div className="col-4">
                                                        <label>Costo:</label>
                                                    </div>
                                                    <div className="col-8">
                                                        <label><b>{funciones.setMoneda(r.COSTO,'Q')}</b></label>    
                                                    </div>
                                                </div>
    
                                                <div className="row border-bottom border-gris">
                                                    <div className="col-4">
                                                        <label>Utilidad:</label>
                                                    </div>
                                                    <div className="col-6">
                                                        <label><b>{funciones.setMoneda(r.UTILIDAD,'Q')}</b></label>    
                                                    </div>
                                                    <div className="col-2">
                                                        <label className="text-danger"><b>{funciones.setPorcentaje(r.MARGEN,'%')}</b></label>
                                                    </div>
                                                </div>
    
                                            </div>
                                        </div>
                                    </div>)
                        })}
    
                    </div>
                    <ResponsiveContainer width="100%" height="100%">
                    <PieChart  width={600} height={500}>
                        <Pie dataKey="VENTAS"
                            isAnimationActive={true}
                            data={data}
                            cx={200}
                            cy={200}
                            outerRadius={200}
                            fill="#8884d5">
                                {data.map((entry, index) => (
                                    <Cell key={`cell-${index}`} fill="#4884d5" label={entry.NOMBRE}/>
                                ))}
                        </Pie>
                        <Tooltip />  
                        </PieChart>
                    </ResponsiveContainer>
                    <div className={this.state.menu}>
                        <a className="floating-btn" onClick={()=>{this.toggleMenu()}}>
                            <i className="fas fa-plus"></i>
                        </a>
                        <menu className="items-wrapper">
                            <a href="#" 
                                data-toggle="tooltip" data-placement="left" title="Análisis de Productos" 
                                className="menu-item  fas fa-box" 
                                onClick={()=>{this.onGetMenu("ANALISIS_PRODUCTOS")}}>P</a>
                            <a href="#" 
                                data-toggle="tooltip" data-placement="left" title="Análisis de Marcas"  
                                className="menu-item  fas fa-briefcase" 
                                onClick={()=>{this.onGetMenu("ANALISIS_MARCAS")}}>M</a>
                            <a href="#" 
                                data-toggle="tooltip" data-placement="left" title="Análisis por Municipio"  
                                className="menu-item  fas fa-map-marker-alt" 
                                onClick={()=>{this.onGetMenu("ANALISIS_MAPA")}}>U</a>
                            <a href="#"
                             data-toggle="tooltip" data-placement="left" title="Historial"  
                             className="menu-item  fas fa-chart-line" 
                             onClick={()=>{this.onGetMenu("ANALISIS_HISTORICO")}}>H</a>
                        </menu>
    
                    </div>
                                      
            </div>
            )
        }
        
    }
}

export default ViewInicio;