import React, {Component} from 'react';

class ViewLoading extends Component {
    constructor(props){
        super(props);
        this.menu = React.createRef()
        this.state = {
            empresas: [],
            menu:'circular-menu',
            loading:false
        };
    }

    render() {
        return(
            <div>
                <h3 className="text-info">
                    <i className="fas fa-spinner fa-spin"></i>  Cargando datos...
                </h3>
            </div>
        )
    }

}

export default ViewLoading;