import React , {Component} from 'react';

export default class ButtonHome extends Component {
    constructor(props) {
        super(props);
        this.state = {
            
        }
    } 

    setBack = ()=>{
        //this.setState({vista:"INICIO"});
        this.props.vista("INICIO");
    }

    render() {
        return(
        <div className="circular-menu">
            <a className="floating-btn" onClick={this.setBack}>
                <i className="fas fa-home"></i>
            </a>
        </div>
        )
    }

}